import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {DatePipe} from '@angular/common';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
// import {PostSettingService} from '../../../../../core/services/post-setting.service';
import {PostsService} from '../../../../../core/services/posts.service';
import {ItemService} from '../../../../../core/services/item.service';
import {ClientService} from '../../../../../core/services/client.service';
import {TaxService} from '../../../../../core/services/tax.service';
import {AuthenticationService} from '../../../../../core/services/authentication.service';
import {CategorysService} from '../../../../../core/services/categorys.service';
import {OptionsService} from '../../../../../core/services/options.service';
import {environment} from '../../../../../../environments/environment';
import { SeoService } from 'src/app/core/services/seo.service';
import { CollectionsService } from 'src/app/core/services/collections.service';
import {NgOption} from '@ng-select/ng-select';
import * as Dropzone from 'dropzone';

@Component({
    selector: 'app-post-create',
    templateUrl: './post-create.component.html',
    styleUrls: ['./post-create.component.scss'],
    providers: [DatePipe]
})

export class PostCreateComponent implements OnInit {
    createPostForm: FormGroup;
    liststatus: NgOption[] = [
        {value: 'yes', label: 'Show'},
        {value: 'no', label: 'Hide'}
    ];
    moduletype:string;
    urlPage:string;
    loginUser: any;
    manufacturers: any;
    seoData: any;
    fileDetails: any;
    selectedFiles = [];
    keywordData:any = [];
    collectionsData:any = [];
    properties: any;
    propertiesHeader: any = [];
    specifications: any = [];
    listposts = [];
    listcategorys = [];
    listTemplate: any = [];
    isFormSubmitted = false;
    isFormLoaded = false;
    datepickerConfig = {
        dateInputFormat: 'YYYY-MM-DD',
        containerClass: 'theme-red'
    };
    @ViewChild('avatardropzone', {static: false}) avatardropzone: ElementRef;
    @ViewChild('imagedropzone', {static: false}) imagedropzone: ElementRef;
    private apiUrl = environment.apiUrl;
    constructor(
        public translate: TranslateService,
        public datepipe: DatePipe,
        private router: Router,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private categorysService: CategorysService,
        private postsService: PostsService,
        private clientService: ClientService,
        private itemService: ItemService,
        private optionsService: OptionsService,
        private taxService: TaxService,
        private seoService: SeoService,
        private collectionsService: CollectionsService,
        private authenticationService: AuthenticationService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
    }

    get postControl() {
        return this.createPostForm.controls;
    }

    ngOnInit() {
        if (this.router.url === '/allnews/create') {
            this.moduletype = 'news';
            this.urlPage = '/allnews';
        } else {
            this.moduletype = 'products';
            this.urlPage = '/allproducts';
            this.getOptions('manufacturers');
            this.getOptions('properties');
            this.getAllMeta();
            this.getAllKeyword();
            // this.getOptions('specifications');
        }
        this.getAllCollections();
        this.getTemplate();
    }

    loadForm() {
        let that = this;
        this.createPostForm = this.formBuilder.group({
            name: [null, Validators.required],
            description: [null],
            category: [null, Validators.required],
            page_title: [null],
            page_description: [null],
            url_and_handle: [null, Validators.required],
            active: [null, Validators.required],
            keyword: [null],
            avatar: [null, Validators.required],
            po_types_indentify:[this.moduletype],
            pages_id: [null, Validators.required],
            price: [null, Validators.required],
            compare_price: [null, Validators.required],
            image: [null],
            seoData: this.formBuilder.array([]),
            seo: [null],
            collections: [null],
            content: [null, Validators.required],
            manufacturers: [null, Validators.required],
            properties: [null],
            variations: this.formBuilder.array([]),
            specifications: this.formBuilder.array([])
        });
        if(this.moduletype == 'products'){
            
        } else {
            this.createPostForm.get('properties').setValidators([]);
            this.createPostForm.get('properties').updateValueAndValidity();
            this.createPostForm.get('manufacturers').setValidators([]);
            this.createPostForm.get('manufacturers').updateValueAndValidity();
            this.createPostForm.get('price').setValidators([]);
            this.createPostForm.get('price').updateValueAndValidity();
            this.createPostForm.get('compare_price').setValidators([]);
            this.createPostForm.get('compare_price').updateValueAndValidity();
        }
        setTimeout(() => {
            that.loadDropzone('avatardropzone');
            that.loadDropzone('imagedropzone');
        });
        this.isFormLoaded = true;
    }
    getAllKeyword() {
        this.seoService.getAllKeyword()
            .subscribe(
                data => {
                    this.keywordData = data;
                });
    }
    getAllCollections() {
        this.collectionsService.getAll()
            .subscribe(
                data => {
                    if(Array.isArray(data)){
                       this.collectionsData = data.filter(res => (res.id != null && res['type'] == "select" && res['po_types_indentify'] == this.moduletype));
                    }
                });
    }
    getAllMeta() {
        this.seoService.getAllMeta()
            .subscribe(
                data => {
                    this.seoData = data;
                });
    }
    
    get controllSpecifications(): FormArray {
        return this.createPostForm.get('specifications') as FormArray;
    }

    get controllVariations(): FormArray {
        return this.createPostForm.get('variations') as FormArray;
    }
    
    private getSeoData(data) {
        return this.formBuilder.group({
            meta: [data.label, Validators.required],
            meta_id: [data.id, Validators.required],
            content: [null]
        });
    }
    private addSeoData(data) {
        const control = <FormArray>this.createPostForm.controls['seoData'];
        control.push(this.getSeoData(data));
    }
    onAddMetaTag(even: any) {
        const control = <FormArray>this.createPostForm.controls['seoData'];
        control.push(this.getSeoData(even));
    }
    onRemoveMetaTag(even: any) {
        (<FormArray>this.createPostForm.get('seoData')).removeAt(even.index)
    }
    changeCategory(even: any) {
        this.specifications = even.specifications;
        console.log(this.specifications);
        (<FormArray>this.createPostForm.get('specifications')).clear();
        if(even.specifications.length > 0) {
            for (let specification of even.specifications) {
                this.addSpecifications(specification);
            }
        }
    }
    private addSpecifications(data) {
        const control = <FormArray>this.createPostForm.controls['specifications'];
        control.push(this.getSpecifications(data));
    }
    private getSpecifications(data) {
        return this.formBuilder.group({
            name: [data.name, Validators.required],
            options_indentify: [data.options_indentify, Validators.required],
            value: [null, Validators.required],
            check: [true]
        });
    }
    builVariations(data, listIndexVariations) {
        let variationsData = [];
        for(let i = 0; i < listIndexVariations.length; i++) {
            variationsData.push(data[i].value[listIndexVariations[i]]);
        }
        const controlVariations = <FormArray>this.createPostForm.controls['variations'];
        controlVariations.push(this.getVariations(variationsData));

        for(let j = listIndexVariations.length - 1; j >= 0; j--) {
            if(listIndexVariations[j] >= data[j].value.length - 1) {
                listIndexVariations[j] = 0;
            } else {
                listIndexVariations[j] = listIndexVariations[j] + 1;
                return this.builVariations(data, listIndexVariations);
            }
        }
        console.log(controlVariations);
    }
    changePropertiesList(even: any) {
        this.propertiesHeader = even;
        console.log(this.propertiesHeader);
        (<FormArray>this.createPostForm.get('variations')).clear();
        if(even.length > 0) {
            let listIndexVariations = [];
            for(let i = 0; i < even.length; i++) {
                listIndexVariations.push(0);
            }
            this.builVariations(even, listIndexVariations);
        }
    }
    private getVariations(data) {
        let variationsData = {
            price: ['', [Validators.required, Validators.min(1)]],
            compare_price: ['', [Validators.required, Validators.min(1)]],
            check: [true]
        };
        for(let i = 0; i < data.length; i++) {
            variationsData[data[i].options_indentify] = [data[i].indentify, Validators.required];
            variationsData[data[i].options_indentify + 'label'] = [data[i].name];
        }
        return this.formBuilder.group(variationsData);
    }

    getOptions(action) {
        this.optionsService.getAll(action).subscribe(
            data => {
                switch (action) {
                    case 'manufacturers':
                        this.manufacturers = data;
                        break;
                    case 'properties':
                        this.properties = data;
                        break;
                    case 'specifications' :
                        this.specifications = data;
                        break;
                }
            });
    }
    getCategorys() {
        this.categorysService.getAll().subscribe(
            data => {
                this.listcategorys = data;
                if(Array.isArray(data)){
                   this.listcategorys = data.filter(res => (res.id != null && res['po_types_indentify'] == this.moduletype));
                }
                this.loadForm();
            });
    }

    getTemplate() {
        this.postsService.getTemplate().subscribe(
            data => {
                if(Array.isArray(data)){
                   this.listTemplate = data.filter(res => res.type == (this.moduletype == 'news' ? 4 : 3));
                }
                this.getCategorys();
            });
    }


    onSubmit() {
        this.isFormSubmitted = true;
        this.createPostForm.patchValue({image: this.selectedFiles});
        if (this.createPostForm.invalid) {
            return;
        }
        this.postsService.create(this.createPostForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('posts.messages.create'), this.translate.instant('posts.title'));
                    this.router.navigate([this.urlPage]);
                });
    }

    loadDropzone(elementDropzone) {
        let that = this;
        let limitFile;
        elementDropzone == 'avatardropzone' ? limitFile = 1 : limitFile = 100;
        let myDropzone = new Dropzone(this[elementDropzone].nativeElement, {
            url: 'https://httpbin.org/post',
            maxFiles: limitFile,
            clickable: true,
            acceptedFiles: 'image/*',
            createImageThumbnails: true,
            init: function () {
                this.on('addedfile', function (file) {
                    const removeButton = Dropzone.createElement('<button class=\'btn btn-sm btn-block\'>' + that.translate.instant('common.remove_file') + '</button>');
                    const _this = this;
                    removeButton.addEventListener('click', function (e) {
                        e.preventDefault();
                        e.stopPropagation();
                        _this.removeFile(file);
                    });

                    file.previewElement.appendChild(removeButton);

                    if (file) {
                        let reader = new FileReader();
                        if(elementDropzone == 'avatardropzone'){
                            reader.onload = (e) => {
                                that.createPostForm.patchValue({avatar: reader.result});
                            };
                        } else {
                            that.fileDetails = file;
                            reader.onload = (e) => {
                                let thisFile = {
                                    uuid: that.fileDetails.upload.uuid,
                                    name: file.name,
                                    extension: file.name.split('.').pop(),
                                    size: file.size,
                                    file: reader.result
                                };
                                that.selectedFiles.push(thisFile);
                            };
                        }
                        reader.readAsDataURL(file);
                    }
                });

                this.on('removedfile', function (file) {
                    if(elementDropzone == 'avatardropzone'){
                        that.createPostForm.patchValue({avatar: null});
                    } else {
                        that.fileDetails = file;
                        that.selectedFiles.forEach((value, index) => {
                            if (value.uuid == that.fileDetails.upload.uuid) {
                                that.selectedFiles.splice(index, 1);
                            }
                        });
                    }
                });

                this.on('error', function (file, message: any) {
                    if (file) {
                        that.toastr.error(message);
                    }
                });
            }
        });
    }
}
