import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {DatePipe} from '@angular/common';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';

import {PostsService} from '../../../../../core/services/posts.service';
import {ItemService} from '../../../../../core/services/item.service';
import {ClientService} from '../../../../../core/services/client.service';
import {TaxService} from '../../../../../core/services/tax.service';
import {AuthenticationService} from '../../../../../core/services/authentication.service';
import {CategorysService} from '../../../../../core/services/categorys.service';
import {OptionsService} from '../../../../../core/services/options.service';
import {environment} from '../../../../../../environments/environment';
import { SeoService } from 'src/app/core/services/seo.service';
import { CollectionsService } from 'src/app/core/services/collections.service';
import {NgOption} from '@ng-select/ng-select';
import * as Dropzone from 'dropzone';

@Component({
    selector: 'app-post-edit',
    templateUrl: './post-edit.component.html',
    styleUrls: ['./post-edit.component.scss']
})

export class PostEditComponent implements OnInit {
    editPostForm: FormGroup;
    selectedItem: number;
    loginUser: any;
    post: any;
    moduletype: string;
    urlPage:string;
    listposts = [];
    listcategorys = [];
    seoData: any;
    fileDetails: any;
    selectedFiles = [];
    keywordData:any = [];
    collectionsData:any = [];
    removedFiles = [];
    manufacturers: any;
    properties: any = [];
    propertiesHeader: any = [];
    specifications: any;
    listTemplate: any = [];
    isFormSubmitted = false;
    isLogoLoaded = true;
    isFormLoaded = false;
    liststatus: NgOption[] = [
        {value: 'yes', label: 'Show'},
        {value: 'no', label: 'Hide'},
    ];
    datepickerConfig = {
        dateInputFormat: 'YYYY-MM-DD',
        containerClass: 'theme-red'
    };
    @ViewChild('avatardropzone', {static: false}) avatardropzone: ElementRef;
    @ViewChild('imagedropzone', {static: false}) imagedropzone: ElementRef;
    private apiUrl = environment.apiUrl;
    constructor(
        public translate: TranslateService,
        public datepipe: DatePipe,
        private router: Router,
        private route: ActivatedRoute,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private postService: PostsService,
        private clientService: ClientService,
        private optionsService: OptionsService,
        private categorysService: CategorysService,
        private itemService: ItemService,
        private taxService: TaxService,
        private seoService: SeoService,
        private collectionsService: CollectionsService,
        private authenticationService: AuthenticationService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
        this.route.paramMap.subscribe(params => {
            this.getById(params.get('id'));
        });
    }

    get postControl() {
        return this.editPostForm.controls;
    }

    ngOnInit() {
        if (this.router.url.indexOf('/allnews/edit') !== -1) {
            this.moduletype = 'news';
            this.urlPage = '/allnews';
        } else {
            this.moduletype = 'products';
            this.urlPage = '/allproducts';
            this.getOptions('manufacturers');
            this.getOptions('specifications');
            this.getAllKeyword();
        }
        this.getAllCollections();
    }

    loadForm() {
        let that = this;
        this.editPostForm = this.formBuilder.group({
            id: [this.post.id, Validators.required],
            name: [this.post.name, Validators.required],
            description: [this.post.description],
            category: [this.post.po_cates_id, Validators.required],
            page_title: [this.post.page_title],
            page_description: [this.post.page_description],
            url_and_handle: [this.post.url_and_handle, Validators.required],
            active: [this.post.active, Validators.required],
            keyword: [this.post.keyword],
            avatar: [this.post.avatar, Validators.required],
            po_types_indentify:[this.moduletype],
            pages_id: [this.post.pages_id, Validators.required],
            manufacturers: [this.post.manufacturers, Validators.required],
            image: [this.post.image],
            price: [this.post.price, Validators.required],
            compare_price: [this.post.compare_price, Validators.required],
            removed_images: [[]],
            seoData: this.formBuilder.array([]),
            seo: [this.post.seo],
            collections: [this.post.collections],
            content: [this.post.content, Validators.required],
            properties: [this.post.properties],
            variations: this.formBuilder.array([]),
            specifications: this.formBuilder.array([])
        });

        if(this.moduletype == 'products'){
            // for (let specification of this.specifications) {
            //     this.addSpecifications(specification);
            // }
        } else {
            this.editPostForm.get('properties').setValidators([]);
            this.editPostForm.get('properties').updateValueAndValidity();
            this.editPostForm.get('manufacturers').setValidators([]);
            this.editPostForm.get('manufacturers').updateValueAndValidity();
            this.editPostForm.get('price').setValidators([]);
            this.editPostForm.get('price').updateValueAndValidity();
            this.editPostForm.get('compare_price').setValidators([]);
            this.editPostForm.get('compare_price').updateValueAndValidity();
        }
        this.editPostForm.patchValue({avatar: this.post.avatar});
        this.editPostForm.patchValue({image: this.post.image});
        setTimeout(() => {
            that.loadDropzone('avatardropzone');
            that.loadDropzone('imagedropzone');
            // const control = <FormArray>this.editPostForm.controls['specifications'];
            // control.push(this.getSpecifications(data));
            for (let categorys of this.listcategorys) {
                if(categorys.id == this.post.po_cates_id){
                    that.changeCategory(categorys, this.post.infotechs);
                }
            }
            
            // console.log(dataProperties);
            // for (let categorys of this.listcategorys) {
            //     if(blockedTile.indexOf("118") != -1){

            //     }
            // }
            // for (var i = 0; i < length; ++i) {
            //     this.properties
            // }
        });
        for (let seo of this.post.seoData) {
            this.onAddMetaTag(seo);
        }

        this.getOptions('properties');
        this.isFormLoaded = true;
    }
    
    getAllCollections() {
        this.collectionsService.getAll()
            .subscribe(
                data => {
                    if(Array.isArray(data)){
                       this.collectionsData = data.filter(res => (res.id != null && res['type'] == "select" && res['po_types_indentify'] == this.moduletype));
                    }
                });
    }
    getAllKeyword() {
        this.seoService.getAllKeyword()
            .subscribe(
                data => {
                    this.keywordData = data;
                });
    }
    getAllMeta() {
        this.seoService.getAllMeta()
            .subscribe(
                data => {
                    this.seoData = data;
                    this.loadForm();
                });
    }
    private getSeoData(data) {
        !data.content ? data.content = null : '';
        return this.formBuilder.group({
            meta: [data.label, Validators.required],
            meta_id: [data.id, Validators.required],
            content: [data.content]
        });
    }
    private addSeoData(data) {
        const control = <FormArray>this.editPostForm.controls['seoData'];
        control.push(this.getSeoData(data));
    }
    onAddMetaTag(even: any) {
        const control = <FormArray>this.editPostForm.controls['seoData'];
        control.push(this.getSeoData(even));
    }
    onRemoveMetaTag(even: any) {
        (<FormArray>this.editPostForm.get('seoData')).removeAt(even.index)
    }
    getById(postId) {
        this.postService.getById(postId).subscribe(data => {
            this.post = data;
            this.getCategorys();
            this.getTemplate();
        });
    }
    getCategorys() {
        this.categorysService.getAll().subscribe(
            data => {
                this.listcategorys = data;
                if(Array.isArray(data)){
                   this.listcategorys = data.filter(res => (res.id != null && res['po_types_indentify'] == this.moduletype));
                }
                // this.loadForm();
            });
    }
    getOptions(action) {
        this.optionsService.getAll(action).subscribe(
            data => {
                switch (action) {
                    case 'manufacturers':
                        this.manufacturers = data;
                        break;
                    case 'properties':
                        this.properties = data;
                        if(this.properties.length > 0){
                            let dataProperties  =  this.properties.filter(x => this.post.properties.indexOf(x.indentify) != - 1);
                            this.changePropertiesList(dataProperties, this.post.product);
                        }
                        break;
                    case 'specifications' :
                        this.specifications = data;
                        break;
                }
            });
    }
    getTemplate() {
        this.postService.getTemplate().subscribe(
            data => {
                if(Array.isArray(data)){
                   this.listTemplate = data.filter(res => res.type == (this.moduletype == 'news' ? 4 : 3));
                }
                this.getAllMeta();
            });
    }
    onSubmit() {
        this.isFormSubmitted = true;
        this.editPostForm.patchValue({image: this.selectedFiles});
        if (this.editPostForm.invalid) {
            return;
        }
        this.editPostForm.patchValue({image: this.selectedFiles});
        this.editPostForm.patchValue({removed_images: this.removedFiles});
        this.postService.update(this.editPostForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('posts.messages.update'), this.translate.instant('posts.title'));
                    this.router.navigate([this.urlPage]);
                });
    }
    get controllSpecifications(): FormArray {
        return this.editPostForm.get('specifications') as FormArray;
    }

    get controllVariations(): FormArray {
        return this.editPostForm.get('variations') as FormArray;
    }
    loadDropzone(elementDropzone) {
        let that = this;
        let limitFile;
        elementDropzone == 'avatardropzone' ? limitFile = 1 : limitFile = 100;
        that[elementDropzone+'element'] = new Dropzone(that[elementDropzone].nativeElement, {
            url: 'https://httpbin.org/post',
            maxFiles: limitFile,
            clickable: true,
            acceptedFiles: 'image/*',
            createImageThumbnails: true,
            init: function () {
                const _this = this;
                if(elementDropzone == 'avatardropzone'){
                    that.editPostForm.get('avatar').value ? _this.previewsContainer.lastChild['hidden'] = true : _this.previewsContainer.lastChild['hidden']= false;    
                } else {
                    that.editPostForm.get('image').value ? _this.previewsContainer.lastChild['hidden'] = true : _this.previewsContainer.lastChild['hidden']= false;    
                }
                this.on('addedfile', function (file) {
                    const removeButton = Dropzone.createElement('<button class=\'btn btn-sm btn-block\'>' + that.translate.instant('common.remove_file') + '</button>');
                    if(_this.options.maxFiles == 1){
                        that.removeDropzoneImage();
                        if(_this.files.length > 1){
                            _this.removeFile(_this.files[0]);
                        }
                    }
                    removeButton.addEventListener('click', function (e) {
                        e.preventDefault();
                        e.stopPropagation();
                        _this.removeFile(file);
                    });

                    file.previewElement.appendChild(removeButton);

                    if (file) {
                        let reader = new FileReader();
                        if(elementDropzone == 'avatardropzone'){
                            reader.onload = (e) => {
                                that.editPostForm.patchValue({avatar: reader.result});
                            };
                        } else {
                            that.fileDetails = file;
                            reader.onload = (e) => {
                                let thisFile = {
                                    uuid: that.fileDetails.upload.uuid,
                                    name: file.name,
                                    extension: file.name.split('.').pop(),
                                    size: file.size,
                                    file: reader.result
                                };
                                that.selectedFiles.push(thisFile);
                            };
                        }
                        reader.readAsDataURL(file);
                    }
                });

                this.on('removedfile', function (file) {
                    if(elementDropzone == 'avatardropzone'){
                        that.editPostForm.patchValue({avatar: null});
                    } else {
                        that.fileDetails = file;
                        that.selectedFiles.forEach((value, index) => {
                            if (value.uuid == that.fileDetails.upload.uuid) {
                                that.selectedFiles.splice(index, 1);
                            }
                        });
                    }
                });

                this.on('error', function (file, message: any) {
                    if (file) {
                        that.toastr.error(message);
                    }
                });
            }
        });
    }

    removeDropzoneImage() {
        this.isLogoLoaded = false;
        this.editPostForm.patchValue({avatar: null});
        this.editPostForm.get('avatar').value ? this['fileDropZone'].previewsContainer.lastChild['hidden'] = true : this['fileDropZone'].previewsContainer.lastChild['hidden']= false;
    }

    removeattachments(id) {
        this.post.image.forEach((value, index) => {
            if (value.id == id) {
                this.post.image.splice(index, 1);
                this.removedFiles.push(id);
            }
        });
    }

    changeCategory(even: any, value) {
        this.specifications = even.specifications;
        (<FormArray>this.editPostForm.get('specifications')).clear();
        if(even.specifications.length > 0) {
            for (var i = 0; i < even.specifications.length; ++i) {
            // for (let specification of even.specifications) {
                this.addSpecifications(even.specifications[i], value[i]);
            }
        }
    }
    private addSpecifications(data, value) {
        const control = <FormArray>this.editPostForm.controls['specifications'];
        control.push(this.getSpecifications(data, value));
    }
    private getSpecifications(data, value) {
        let valueSpecifications = (value && value.options_value_indentify) ? value.options_value_indentify : null;
        return this.formBuilder.group({
            name: [data.name, Validators.required],
            id: [data.id, Validators.required],
            options_indentify: [data.options_indentify, Validators.required],
            value: [valueSpecifications, Validators.required],
            check: [true]
        });
    }
    builVariations(data, listIndexVariations, LoadValues) {
        let variationsData = [];
        for(let i = 0; i < listIndexVariations.length; i++) {
            variationsData.push(data[i].value[listIndexVariations[i]]);
        }
        const controlVariations = <FormArray>this.editPostForm.controls['variations'];
        controlVariations.push(this.getVariations(variationsData, LoadValues[controlVariations.length]));

        for(let j = listIndexVariations.length - 1; j >= 0; j--) {
            if(listIndexVariations[j] >= data[j].value.length - 1) {
                listIndexVariations[j] = 0;
            } else {
                listIndexVariations[j] = listIndexVariations[j] + 1;
                return this.builVariations(data, listIndexVariations, LoadValues);
            }
        }
    }

    changePropertiesList(even: any, LoadValues) {
        this.propertiesHeader = even;
        (<FormArray>this.editPostForm.get('variations')).clear();
        if(even.length > 0) {
            let listIndexVariations = [];
            for(let i = 0; i < even.length; i++) {
                listIndexVariations.push(0);
            }
            this.builVariations(even, listIndexVariations, LoadValues);
        }
    }
    private getVariations(data, LoadValues) {
        let variationsData = {};
        for(let i = 0; i < data.length; i++) {
            variationsData[data[i].options_indentify] = [data[i].indentify, Validators.required];
            variationsData[data[i].options_indentify + 'label'] = [data[i].name, Validators.required];
        }
        if(LoadValues && LoadValues.id && LoadValues.price){
            variationsData['price'] = [LoadValues.price, [Validators.required, Validators.min(1)]];
            variationsData['compare_price'] = [LoadValues.compare_price, [Validators.required, Validators.min(1)]];
            variationsData['id'] = [LoadValues.id];
            variationsData['sku'] = [LoadValues.sku];
            variationsData['check'] = [LoadValues.status];
        } else {
            variationsData['compare_price'] = ['', [Validators.required, Validators.min(1)]];
            variationsData['price'] = ['', [Validators.required, Validators.min(1)]];
            variationsData['check'] = [true];
        }
        
        return this.formBuilder.group(variationsData);
    }

}
