import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {NgxPermissionsGuard} from 'ngx-permissions';

import {PostListsComponent} from './pages/post-lists/post-lists.component';
import {PostCreateComponent} from './pages/post-create/post-create.component';
import {PostEditComponent} from './pages/post-edit/post-edit.component';
// import {PostViewComponent} from './pages/post-view/post-view.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: '',
            canActivate: [NgxPermissionsGuard],
            component: PostListsComponent
        },
        {
            path: 'create',
            canActivate: [NgxPermissionsGuard],
            data: {
                breadcrumbs: {
                    text: 'common.create',
                    icon: 'fa fa-external-link',
                    show: true,
                    isHome: true
                },
                permissions: {
                    only: ['admin', 'super_admin', 'posts_create']
                }
            },
            component: PostCreateComponent
        },
        {
            path: 'edit/:id',
            canActivate: [NgxPermissionsGuard],
            data: {
                breadcrumbs: {
                    text: 'common.edit',
                    icon: 'fa fa-external-link',
                    show: true,
                    isHome: true
                },
                permissions: {
                    only: ['admin', 'super_admin', 'posts_edit']
                }
            },
            component: PostEditComponent
        },
        // {
        //     path: 'detail/:id',
        //     canActivate: [NgxPermissionsGuard],
        //     data: {
        //         breadcrumbs: {
        //             text: 'common.detail',
        //             icon: 'fa fa-external-link'
        //         },
        //         permissions: {
        //             only: ['admin', 'super_admin', 'posts_view']
        //         }
        //     },
        //     component: PostViewComponent
        // }
    ]
}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class PostsRoutingModule {
}
